
import { defineComponent, onMounted, reactive, ref } from 'vue'
import { useRoute, useRouter } from "vue-router"
import { useStore } from "vuex"
import homeHeader from "@/components/header.vue"
import { getCategoryDetail } from "@/api/index.ts"
import { ImagePreview, Toast } from 'vant';
import { listService, paramsList, shopList } from "./listInfo"

export default defineComponent({
  name: 'categoryDetail',
  components: {
    homeHeader,
    [ImagePreview.Component.name]: ImagePreview.Component
  },
  setup(props, context){
    const route = useRoute()
    const router = useRouter()
    const store = useStore()
    const state = reactive({
      listService,
      paramsList,
      shopList, 
      selectShop: false, // 选择商品
      cartShow: false, // 购物车
      params: false, // 参数
      service: false, // 服务
      isShow: false, // 是否分享
      showCoupon: false, // 是否领券
      cartSelect: 0,
      options: [
        { name: '微信', icon: 'wechat' },
        { name: '微博', icon: 'weibo' },
        { name: '复制链接', icon: 'link' },
        { name: '分享海报', icon: 'poster' },
        { name: '二维码', icon: 'qrcode' },
      ],
      swipeObj: {
        id: 0,
        desc: "",
        discountMoney: 0,
        money: 0,
        tag : "",
        type: "",
        sales: 0,
        imgUrl: "" ,
        checked: false
      }
    })

    // 数据初始化
    onMounted(() => {
      let { id, pid } = route.query
      // console.log("id-pid",id, pid)
      let data = {
        id: id,
        pid: pid
      }
      getCategoryDetail(data).then(res => {
        console.log("77777", res)
        // state.swipeObj = 
        if(res.code == 200){
          state.swipeObj = res.data
          console.log("state", state.swipeObj)
        }
      })
    })

    // 领券
    const handleCoupon = () => {
      state.showCoupon = true
    }

    // 返回
    const handleBack = () => {
      router.go(-1)
    }

    // 预览图片
    const handlePreview = () => {
      ImagePreview([
        state.swipeObj.imgUrl
      ]);
    }

    // 分享
    const handleShare = () => {
      state.isShow = true
    }

    // 选择分享
    const onSelect = (val: any) => {
      console.log("onSelect--", val.name)
    }

    // 加入购物车
    const handleAddCart = () => {
      console.log("store--", store)
      // mutations
      state.cartShow = true
      state.selectShop = false
    }

    // 服务
    const handleService = () => {
      state.service = true
    }

    // 参数
    const handleParams = () => {
      state.params = true
    }

    // 选择颜色
    const handleCartSelect = (item: any, index: number) => {
      console.log("xxxx---slect---", item, index)
      state.cartSelect = index
    }

    // 加入购入车---
    const handleCartShop = () => {
      state.swipeObj.checked = false
      store.commit("setShowList", state.swipeObj)
      Toast("成功加入购物车")
    }
    // 选择商品
    const handleSelectShop = () => {
      state.cartShow = true
      state.selectShop = true
    }

    return {
      state,
      handleBack,
      handlePreview,
      handleShare,
      onSelect,
      handleCoupon,
      handleAddCart,
      handleService,
      handleParams,
      handleCartSelect,
      handleCartShop,
      handleSelectShop
    }
  }
})
